@mixin mobile {
  @media (max-width: $mobile-width) {
    @content;
  }
}
@mixin tablet {
  @media (max-width: $tablet-width) {
    @content;
  }
}
@mixin minipc {
  @media (max-width: $minipc-width) {
    @content;
  }
}
@mixin smallpc {
	@media (max-width: $smallpc-width) {
	  @content;
	}
  }
